@import 'src/scss/typography';

:root {
    --tooltip-text-color: var(--color-white);
    --tooltip-background-color: var(--color-navy-600);
    --tooltip-vertical-margin: 36px;
    --tooltip-horizontal-margin: 12px;
    --tooltip-arrow-size: 6px;
}

.tooltipWrapper {
    display: inline-block;
    position: relative;
}

.tooltipContent {
    @include body-3-regular;
    position: absolute;
    border-radius: 6px;
    left: 50%;
    transform: translateX(-50%);
    padding: 6px 12px;
    color: var(--tooltip-text-color);
    background: var(--tooltip-background-color);

    z-index: 100;
    white-space: nowrap;
}

.tooltipShow {
    animation: fadeIn linear 200ms;
    -webkit-animation: fadeIn linear 200ms;
    -moz-animation: fadeIn linear 200ms;
    -o-animation: fadeIn linear 200ms;
    -ms-animation: fadeIn linear 200ms;
}

.tooltipHide {
    animation: fadeOut linear 200ms;
    -webkit-animation: fadeOut linear 200ms;
    -moz-animation: fadeOut linear 200ms;
    -o-animation: fadeOut linear 200ms;
    -ms-animation: fadeOut linear 200ms;
}

.tooltipContent.top {
    top: calc(var(--tooltip-vertical-margin) * -1);
}

.tooltipContent.top::before {
    top: 100%;
    border-top-color: var(--tooltip-background-color);
}

.tooltipContent.right {
    left: calc(100% + var(--tooltip-horizontal-margin));
    top: 50%;
    transform: translateX(0) translateY(-50%);
}

.tooltipContent.right::before {
    left: calc(var(--tooltip-arrow-size) * -1);
    top: 50%;
    transform: translateX(0) translateY(-50%);
    border-right-color: var(--tooltip-background-color);
}

.tooltipContent.bottom {
    bottom: calc(var(--tooltip-vertical-margin) * -1);
}

.tooltipContent.bottom::before {
    bottom: 100%;
    border-bottom-color: var(--tooltip-background-color);
}

.tooltipContent.left {
    left: auto;
    right: calc(100% + var(--tooltip-horizontal-margin));
    top: 50%;
    transform: translateX(0) translateY(-50%);
}

.tooltipContent.left::before {
    right: calc(var(--tooltip-arrow-size) * -2);
    top: 50%;
    transform: translateX(0) translateY(-50%);
    border-left-color: var(--tooltip-background-color);
}

.showTooltipArrow {
    &::before {
        content: ' ';
        left: 50%;
        border: solid transparent;
        border-radius: 1px;
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-width: var(--tooltip-arrow-size);
        margin-left: calc(var(--tooltip-arrow-size) * -1);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0.25;
    }
    100% {
        opacity: 1;
    }
}

@-moz-keyframes fadeIn {
    0% {
        opacity: 0.25;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0.25;
    }
    100% {
        opacity: 1;
    }
}

@-o-keyframes fadeIn {
    0% {
        opacity: 0.25;
    }
    100% {
        opacity: 1;
    }
}

@-ms-keyframes fadeIn {
    0% {
        opacity: 0.25;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 0.75;
    }

    100% {
        opacity: 0;
    }
}

