:root {
    --toastify-toast-width: 375px !important;
    --toastify-toast-min-height: 50px !important;
    --toastify-toast-max-height: 80px !important;
    --toastify-font-family: 'Poppins' !important;
    --toastify-text-color-light: var(--color-navy-600) !important;
    --toastify-text-color-dark: var(--color-navy-600) !important;
    --toastify-text-color-info: var(--color-blue-600) !important;
    --toastify-text-color-success: var(--color-navy-600) !important;
    --toastify-text-color-warning: var(--color-yellow-600) !important;
    --toastify-color-error: var(--color-red-600) !important;
    --toastify-color-info: var(--color-blue-600) !important;
    --toastify-color-success: var(--color-green-600) !important;
    --toastify-color-warning: var(--color-yellow-600) !important;
    --toastify-color-error: var(--color-red-600) !important;
    --toastify-color-progress-info: var(--toastify-color-info);
    --toastify-color-progress-success: var(--toastify-color-success);
    --toastify-color-progress-warning: var(--toastify-color-warning);
    --toastify-color-progress-error: var(--toastify-color-error);
}

// toast configuration
.Toastify__toast-container {
    width: fit-content !important;
    max-width: var(--toastify-toast-width) !important;
    max-height: none !important;
}

.Toastify__toast {
    border-radius: 8px !important;
    box-shadow: 0px 2px 10px rgba(206, 214, 224, 0.75) !important;
    padding-right: 40px !important;
}

.Toastify__toast-body {
    padding: 4px !important;
    margin-left: 8px !important;
    font-size: 14px !important;
    font-weight: 500;
}

.Toastify__close-button {
    display: none;
}

.Toastify__toast-icon {
    width: 28px !important;
    font-size: 12px;
    margin-right: 15px !important;
    margin-bottom: auto;
}

.Toastify__progress-bar {
    height: 3px !important;
}
